import React from "react";
import affiche_2 from "./../../assets/ARTICLE.png";

const IntroProgramme = ({ boolProgramme }) => {
  return (
    <div className="intro_programme">
      <h1>Notre programme</h1>
      <p>
        {/* Notre programme en bref (pour l'instant...) <br /> */}
        {/* <br /> */}
        Le programme du BDE est au coeur de notre liste et de notre identité.
        Pour maximiser sa visibilité, chaque jour des nouvelles affiches vont
        être accrochées partout sur le campus qui vous renverront vers notre
        site. L'intégralité du programme sera publiée lundi ! En attendant
        profitez des différentes activités,
        <br />
        see you later alligator !
      </p>
      {boolProgramme === "true" && (
        <p>
          Télécharger notre programme ici :
          <a href="./img/Londres_1.jpg" download="Programme Elémentaire">
            Programme
          </a>
        </p>
      )}
      <img src={affiche_2} alt="presentation" />
    </div>
  );
};

export default IntroProgramme;
