import React, { useEffect } from "react";
import page_ from "../fonction_js/page_info";

const Liens = ({ widthScreen, heightScreen }) => {
  function resizeImg() {
    let img = document.getElementsByClassName("liens_container");
    for (let value of img) {
      value.style.height = `${page_.getYmax()}px`;
    }
  }
  let date = new Date(Date.now());

  // useEffect(() => {
  //   let date = new Date(Date.now());
  //   // console.log(date.getHours());
  // }, []);

  useEffect(() => {
    resizeImg();
  }, [widthScreen, heightScreen]);

  return (
    <div className="liens_container">
      <div className="sousContainer">
        <h1>Voici des liens utiles</h1>
        <h2>Les liens pour s'inscrire</h2>
        <div className="list_liens_container">
          <a href="https://docs.google.com/forms/d/e/1FAIpQLSc1nLxoFrbkr4Ou2CS_JAD9Mw2bjxIXxkx-BQBVKUzW4_jSOw/viewform">
            Grosse activité !
          </a>

          {date.getHours() >= 8 &&
          date.getDay() == 1 &&
          date.getHours() < 19 ? (
            <a href="https://docs.google.com/forms/d/1GHMiuvIvcT7sFUEj4GTz2YNXfpu7nNjL6ApKgdtocN4/">
              Le repas !
            </a>
          ) : (
            <a href="/">Le repas ! </a>
          )}

          <a href="/">Hotline !</a>
        </div>
        <p>
          NB : Les réponses enregistrées avant la date d'ouverture ne seront pas
          prise en compte.
        </p>
      </div>
    </div>
  );
};

export default Liens;
